.contact {
  width: 1170px;
  margin: 80px auto auto auto;
  background-image: url(../img/banner-bottom.jpg);
  background-position: left;
  background-repeat:no-repeat;
  height: 455px;
  position: relative;
  &__form {
    position: absolute;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 557px;
    height: 216px;
    padding: 50px 49px 49px 50px;
    background: #FFFFFF;
    box-shadow: 0px 4px 30px rgba(41, 54, 66, 0.1);
    border-radius: 5px;
    right: -40px;
    top: 80px;
  }
  &__head {
    font-weight: 600;
    width: 100%;
    text-align: center;
    font-size: 25px;
    line-height: 32px;
    color: $darck;
    height: min-content;
  }
  &__text {
    margin-top: 15px;
    width: 100%;
    text-align: center;
  }
  &__input {
    width: 261px;
    height: 50px;
    border:1px solid #D7D9DF;
    border-radius:none;
    &:last-child {
      margin-left: 19px;
    }
  }
  &__button {
    font-size: 12px;
    line-height: 15px;
    font-weight: 700;
    color: white;
    letter-spacing: 2px;
    text-transform: uppercase;
    border: none;
    background-color: $orange;
    width: 176px;
    height: 50px;
    margin-left: auto;
    margin-right: auto;
  }
}
