.paopup {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.082);
  top: 0;
  left: 0;
  &__container {
    position: relative;
    width: 728px;
    height: 444px;
    background-color:$white;
    background-image: url(../img/paopup.jpg);
    background-position: left;
    background-repeat: no-repeat;
    margin: 25% auto auto auto;
  }
  &__form {
    width: 280px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    float: right;
    padding: 72px 61px 0 59px;
  }
  &__head {
    text-align: center;
    font-weight: 600;
    font-size: 25px;
    line-height: 170%;
    color: #48494D;
  }
  &__input {
    height: 50px;
    border:1px solid #D7D9DF;
    border-radius:none;
    margin-top: 20px;
  }
  &__button {
    border: none;
    background-color: $orange;
    letter-spacing: 2px;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 12px;
    line-height: 15px;
    color: #FFFFFF;
    width: 176px;
    height: 50px;
    margin: 20px auto auto auto;
  }
  &__success {
    position: absolute;
    background-color:$white;
    width: 728px;
    height: 444px;
  }
  &__success-text {
    font-weight: 500;
    font-size: 25px;
    line-height: 170%;
    text-align: center;
    color: #48494D;
    width: 399px;
    margin: 179px auto auto auto;
  }
  &__close-btn {
    position: absolute;
    z-index: 1;
    right: 20px;
    top: 22px;
    height: 22px;
    width: 22px;
    color: transparent;
    border: none;
    background-color: transparent;
    background-image: url(../img/icon/close.svg);
  }
}
